<template>
  <div class="hello">
    <ul>
      <li v-for="(article, index) in newsArticles" :key="index">
        <h3>{{article.title}}</h3>
        <cite>{{article.author}}</cite>
        <p>{{article.content}}</p>
        <hr>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'HelloWorld',
  data () {
    return {
      newsArticles: [],
      error: null
    }
  },
  created() {
    axios.get('https://newsapi.org/v2/everything?q=bitcoin&from=2018-12-08&sortBy=publishedAt&apiKey=b99392448bbf4368805bf963e4d94ffc').then(
      response => {
        // this.apiResult = response.data
        this.newsArticles = response.data.articles
        console.log(this.newsArticles)
      }
    ).catch(err => {
      (this.error = err)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.red-text {
  color: red;
}

.green-text {
  color: green;
}
</style>



